import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { AppComponent } from './app.component'
import { NotFoundComponent } from './pages/not-found/not-found.component'
import { SharedModule } from './components/shared/shared.module'

const routes: Routes = [
  {
    path: '', 
    redirectTo: '/profissional',
    pathMatch: 'full'
  },
  // {
  //   path: '',
  //   data: { title: 'Diz Quem Faz' },
  //   component: HomeComponent
  // },
  {
    path: 'profissional',
    data: { title: 'Anuncie seus Servios' },
    loadChildren: 'app/pages/professional-back-and/professional-back-and.module#ProfessionalBackAndModule'
  },
  {
    path: 'style-guide',
    data: { title: 'Guia de Estilos' },
    loadChildren: 'app/pages/style-guide/style-guide.module#StyleGuideModule'
  },
  {
    path: 'como-funciona',
    data: { title: 'Como Funciona' },
    loadChildren: 'app/pages/how-it-works/how-it-works.module#HowItWorksModule'
  },
  {
    path: '**',
    data: { title: 'Pgina no encontrada' },
    component: NotFoundComponent
  }
]

@NgModule({
  declarations: [
    NotFoundComponent
  ],
  imports: [
    SharedModule,
    RouterModule.forRoot(routes)
  ],
  exports: [ RouterModule ]
})
export class AppRoutingModule { }



